@import '../../styles/defaults.scss';
@import './keyframes.scss';

$border-right: 6px;
$text-stroke-color: rgba(200, 200, 200, 1);
$animation-color: $accent;
$fs-size: 3em;

.navButton-wrapper {
    width: fit-content;
	.navButton-button {
        margin: 0;
        padding: 0;
        height: auto;
        background: transparent;
        padding: 0;
        border: none;
    }
	.navButton-button {
        letter-spacing: 3px;
        text-decoration: none;
        font-size: $fs-size;
        font-family: "vcrfont";
        position: relative;
        text-transform: uppercase;
        color: transparent;
        -webkit-text-stroke: 2px $text-stroke-color;
    }
    .navButton-textHover {
        visibility: hidden;
        position: absolute;
        content: attr(data-text);
        color: $animation-color;
        width: 0%;
        inset: 0;
        border-right: $border-right solid $animation-color;
        overflow: hidden;
        transition: 0.5s;
        -webkit-text-stroke: 1px $animation-color;
    }
    .navButton-button:hover .navButton-textHover {
        visibility: visible;
        width: 100%;
        filter: drop-shadow(0 0 23px $animation-color);
    }

}
